body {
    margin: 0;
    padding: 0;
    max-width: 1200px;
    margin-inline-start: auto;
    margin-inline-end: auto;
    margin-block-start: 50px;
    background-color: lightgrey;
}

#root {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 16px;
    column-gap: 16px;
}

.about_cart {
    padding: 16px;
    border-radius: 12px;
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    background-color: white;
}

img {
    border-radius: 12px;
    width: 100%;
}

.about_cart__about_me > h2 {
    margin-block-end: 0;
}

.contact {
    display: flex;
}

.contact__email {
    text-decoration: none;
    color: black;
    margin-inline-start: 16px;
    margin-block-start: 5px;
}

.contact::before {
    content: url('./assets/email-24px.svg');
}